import CityData from '@/data/city';

export default {
	data() {
		return {
			list: CityData,
			bar: {
				top: 0,
				itemH: 0
			},
			curr: {},
			showT: false,
			label: null,
			keyWord: ''
		};
	},

	computed: {
		flist() {
			const match = d => d.label.includes(this.keyWord);

			return this.list
				.filter(e => e.children.find(match))
				.map(e => {
					e.children = e.children.filter(match);
					return e;
				});
		}
	},

	mounted() {
		const query = uni.createSelectorQuery().in(this);

		query
			.select(`.cl-list-index__bar-ul`)
			.boundingClientRect(res => {
				this.bar.top = res.top;
				this.bar.itemH = parseInt(res.height / this.list.length);
			})
			.exec();
	},

	methods: {
		todo(e) {
			this.showT = true;
			this.curr = e;
		},

		barMove(e) {
			const index = parseInt((e.touches[0].clientY - this.bar.top) / this.bar.itemH);
			this.curr = this.list[index];
		},

		barEnd() {
			this.label = this.curr.label;
			this.showT = false;
		},

		selectRow(item, parent) {
			this.$emit('select', item, parent);
		}
	}
};
